@import "https://cdn.jsdelivr.net/npm/reset-css@5.0.1/reset.min.css";

* {
  box-sizing: border-box;
}

input {
  all: unset;
  box-sizing: border-box;
  appearance: none;
}

body {
  background-color: black;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 14px;
  color: white;
}

button {
  background-color: white;
  color: black;
}

a {
  text-decoration: none;
  color: inherit;
}

form {
  width: 100%;
}

.nweet-input::placeholder {
  color: white;
  opacity: 0.9;
}

.container {
  width: 100%;
  max-width: 320px;
  display: flex;
  flex-direction: column;
}
.write_icon_box{
  position: relative;
  font-size: 20px;
  background-color: #04aaff;
  width: 60px;
  height: 60px;
  border-radius: 3em;
  margin: auto;
}
.write_icon{
  position: absolute;
  padding: 10px;
  top: 10px;
  left: 10px;
}
.formInput {
  width: 100%;
  padding: 10px 20px;
  border-radius: 20px;
  border: 1px solid black;
  text-align: center;
  background-color: white;
  color: black;
}

.formBtn {
  cursor: pointer;
  width: 100%;
  padding: 7px 20px;
  text-align: center;
  color: white;
  border-radius: 20px;
  background-color: #04aaff;
  cursor: pointer;
}

.cancelBtn {
  cursor: pointer;
  background-color: tomato;
}

/* Profile Page */

.profileForm {
  border-bottom: 1px solid rgba(255, 255, 255, 0.9);
  padding-bottom: 30px;
  width: 100%;
  display: flex;
  flex-direction: column;
}
.profileForm h2, .my__mweets{
  font-size: 20px;
  text-align: center;
  margin-top: 50px;
  margin-bottom: 20px;
  font-weight: 500;
}
.profile__attachment img{
  width: 100px;
  height: 100px;
  margin-bottom: 10px;
  border-radius: 5em;
}
.profile__picture{
  text-align: center;
}
.profile__picture img{
  width: 150px;
  height: 150px;
  border-radius: 6em;
  margin: 10px;
}
.profile_modify_btn{
  margin-bottom: 10px;
}
.my__mweets{
  margin-top: 0px;
}
.logOut {
  margin-top: 50px;
  margin-bottom: 30px;
}

/* Nwitter Factory */

.factoryForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.factoryInput__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  position: relative;
  margin-bottom: 20px;
  width: 70%;
}

.factoryInput__input {
  flex-grow: 1;
  height: 50vh;
  padding: 0px 20px;
  color: white;
  border: 1px solid #04aaff;
  border-radius: 20px;
  font-weight: 500;
  font-size: 12px;
}

.factoryInput__arrow {
  position: absolute;
  right: 0;
  background-color: #04aaff;
  height: 40px;
  width: 40px;
  padding: 10px 0px;
  text-align: center;
  border-radius: 20px;
  color: white;
}

.factoryInput__label {
  color: #04aaff;
  cursor: pointer;
}

.factoryInput__label span {
  margin-right: 10px;
  font-size: 12px;
}

.factoryForm__attachment {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.factoryForm__attachment img {
  height: 80px;
  width: 80px;
  border-radius: 40px;
}

.factoryForm__clear {
  color: #04aaff;
  cursor: pointer;
  text-align: center;
}

.factoryForm__clear span {
  margin-right: 10px;
  font-size: 12px;
}

/* Nweet */

.nweetEdit .formBtn {
  cursor: pointer;
  margin-top: 15px;
  margin-bottom: 5px;
}

.nweet {
  margin-bottom: 20px;
  background-color: white;
  width: 100%;
  /* height: 300px; */
  max-width: 320px;
  padding: 20px;
  border-radius: 10px;
  position: relative;
  display: flex;
  flex-direction: column;
  color: rgba(0, 0, 0, 0.8);
}
.one_mweet{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.one_mweet h2{
  font-size: 20px;
  margin-top: 35px;
  font-weight: 600;
}
.nweet h4 {
  font-size: 16px;
  margin-top: 10px;
}

/* .nweet img {
  right: -10px;
  top: 20px;
  position: absolute;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  margin-top: 10px; 프사처럼 사용할 수 있을듯
} */
.nweet .mweet__attachment{
  /* left: -10px; */
  top: 20px;
  position: relative;
  width: 100%;
  height: 70%;
  margin-bottom: 10px;
  margin-top: 10px;
  border-radius: 5px;
}
/* .nweet__actions {
  position: absolute;
  right: 10px;
  top: 10px;
} */

.nweet__actions span {
  cursor: pointer;
}

.nweet__actions span:first-child {
  margin-right: 10px;
}

/* Auth */
.authtitle h1{
  text-align: center;
  margin-bottom: 15px;
}
.authContainer {
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
  align-items: center;
}

.authBtns {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 320px;
}

.authBtn {
  cursor: pointer;
  border-radius: 20px;
  border: none;
  padding: 10px 0px;
  font-size: 12px;
  text-align: center;
  width: 150px;
  background: white;
  cursor: pointer;
}

/* AuthForm */

.authInput {
  max-width: 320px;
  width: 100%;
  padding: 10px;
  border-radius: 30px;
  background-color: rgba(255, 255, 255, 1);
  margin-bottom: 10px;
  font-size: 12px;
  color: black;
}

.authSubmit {
  text-align: center;
  background: #04aaff;
  color: white;
  margin-top: 10;
  cursor: pointer;
}

.authError {
  color: tomato;
  text-align: center;
  font-weight: 500;
  font-size: 12px;
}

.authSwitch {
  color: #04aaff;
  cursor: pointer;
  margin-top: 10px;
  margin-bottom: 50px;
  display: block;
  font-size: 12px;
  text-decoration: underline;
}
.authpwdcheck_span{
  margin: 10px;
  text-align: center;
}
.authpwdcheck{
  width: fit-content;
  float: right;
  margin-bottom: 5px;
}
/* Detail Page */
.detail_container{
  display: flex;
  flex-direction: column;
  /* border: 2px solid white; */
  background-color: white;
  border-radius: 1.5em;
  max-width: 320px;
  width: 320px;
}
.detail_profile{
  display: flex;
  flex-direction: row;
  margin-top: 20px;
  margin-left: 20px;
}
.detail_profile p{
  display: flex;
  margin-left: 20px;
  font-size: 20px;
  align-items: center;
  color: black;
}
.detail_contents{
  font-size: 18px;
  width: fit-content;
  height: fit-content;
  padding: 20px;
}
.detail_contents h4{
  margin-bottom: 20px;
  margin-left: 10px;
  display: flex;
  flex-wrap: wrap;
  color: black;
}
.detail_attachment{
  display: block;
  margin: auto;
  width: 250px;
  height: auto;
}
.mweet__actions{
  /* border: 1px solid white; */
  margin-left: 70px;
}
.mweet__actions button{
  margin-right: 10px;
  background: none;
  color: black;
  border: none;
}
.mweet__actions button:hover{
  cursor: pointer;
}
.edit__icon{
  color: black;
}
/* edit page*/
.edit__attachment{
  display: block;
  margin: auto;
}
.edit__attachment img{
  margin: 10px;
  width: 250px;
  height: auto;
  object-fit: cover;
}